import Vue from "vue";
import VueRouter from "vue-router";
import App from "./App.vue";
import Therapy from "./Therapy.vue";
import Fees from "./Fees.vue";
import Founder from "./Founder.vue";
import DISC from "./DISC.vue";
import Coaching from "./Coaching.vue";
import Workplace from "./Workplace.vue";

Vue.config.productionTip = false;
Vue.use(VueRouter);

const routes = [
  { path: "/", component: App },
  { path: "/therapy", component: Therapy },
  { path: "/disc", component: DISC },
  { path: "/coaching", component: Coaching },
  { path: "/fees", component: Fees },
  { path: "/founder", component: Founder },
  { path: "/workplace-wellbeing", component: Workplace },
  { path: "/home", component: App },
  { path: "/welcome", component: App },
  { path: "/offerings", component: App },
  { path: "/contact", component: App },
  { path: "/belinda", redirect: "/founder" },
  { path: "/belinda-sidhu", redirect: "/founder" },
  // { path: "/:catchAll(.*)", component: App },
];

const router = new VueRouter({
  mode: "history",
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      return {
        selector: to.hash,
        behavior: "smooth",
      };
    }
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  },
});

new Vue({
  router,
}).$mount("#app");
