<template>
  <section class="section" id="contact">
    <div class="section-heading">
      <h3 class="title is-2">Get in touch</h3>
      <p><strong>Take your first step</strong></p>
    </div>
    <br />
    <div class="columns">
      <div class="column is-6 is-offset-3">
        <div class="box">
          <div class="field">
            <label class="label">Name</label>
            <div class="control">
              <input
                class="input"
                type="text"
                v-model="name"
                placeholder="Your name"
              />
            </div>
          </div>

          <div class="field">
            <label class="label">Email</label>
            <div class="control has-icons-left">
              <input
                class="input"
                type="email"
                placeholder="Your email"
                v-model="email"
              />
              <span class="icon is-small is-left">
                <i class="fas fa-envelope"></i>
              </span>
            </div>
          </div>

          <div class="field">
            <label class="label">Phone</label>
            <div class="control has-icons-left">
              <input
                class="input"
                placeholder="Your phone number"
                v-model="phone"
              />
              <span class="icon is-small is-left">
                <i class="fas fa-phone"></i>
              </span>
            </div>
          </div>

          <div class="field">
            <label class="label">Message</label>
            <div class="control">
              <textarea
                class="textarea"
                placeholder="Your message"
                v-model="message"
              ></textarea>
            </div>
          </div>
        </div>
      </div>
      <div class="column">
        <br />
        <p class="service-times">
          We will aim to get back to you within one working day.
        </p>
        <br />
        <p class="service-times">
          Please let us know whether you would prefer to be contacted by email
          or by phone, and if you're happy for us to leave a voicemail if you're
          unavailable.
        </p>
        <br />
        <p class="email-text">
          You can also email us at
          <a
            class="email link"
            target="_blank"
            href="mailto:info@bewellconsultancy.com"
            >info@bewellconsultancy.com</a
          >
          or call us on +44 7340 041 147.
        </p>
        <br />
        <p class="email-text">
          For press or media enquiries please contact us at
          <a
            class="email-link"
            target="_blank"
            href="mailto:press@bewellconsultancy.com?subject=Press enquiry"
            >press@bewellconsultancy.com</a
          >
        </p>
      </div>
    </div>
    <button @click="sendMessage" :class="buttonType">
      <span class="icon">
        <i :class="iconType"></i>
      </span>
      <span>{{ buttonText }}</span>
    </button>
    <p class="warning" v-if="incomplete">Please complete all fields</p>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "Contact",
  props: {},
  data: function () {
    return {
      name: "",
      email: "",
      phone: "",
      message: "",
      incomplete: false,
      buttonText: "Submit",
      iconType: "fas fa-envelope",
      buttonType: "button is-centered is-active is-medium",
    };
  },
  methods: {
    sendMessage() {
      if (!this.name || !this.email || !this.phone || !this.message) {
        return (this.incomplete = true);
      } else {
        axios({
          method: "post",
          // url: "http://localhost:4242/contact-form",
          url: "https://be-well-consultancy-backend.herokuapp.com/contact-form",
          data: {
            name: this.name,
            email: this.email,
            phone: this.phone,
            message: this.message,
          },
        });
        this.incomplete = false;
        // changes button message and colour to show mesage received
        this.buttonText = "Received";
        this.iconType = "fas fa-check";
        this.buttonType = "button is-centered is-link is-medium is-success";

        // reverts button back to "Submit" after 1.5 seconds
        return setTimeout(this.revertButton, 1500);
      }
    },
    revertButton() {
      this.buttonText = "Submit";
      this.buttonType = "button is-centered is-active is-medium";
      this.iconType = "fas fa-envelope";
      this.name = "";
      this.email = "";
      this.message = "";
      this.phone = "";
    },
  },
};
</script>

